import * as React from 'react';
import { graphql } from 'gatsby';
import useIntlHelper from '~src/_i18n/helpers/useIntlHelper';
import Layout from '~src/components/Layout/Layout';
import { NewHero } from '~src/components/Landing/xxHero/NewHero';
import { PostFeed } from '~src/components/Sections/Posts/PostFeed/PostFeed';
import { FilmFeed } from '~src/components/Sections/Filmography/FilmFeed/FilmFeed';

const NewIndex: React.FC<{ data }> = ({ data }) => {
	const stickyRef: React.RefObject<HTMLDivElement> = React.useRef(null);
	const { formatMessage } = useIntlHelper();
	return (
		<>
			<Layout template="home" stickyRef={stickyRef} header={<NewHero />}>
				<div className="inner" ref={stickyRef}>
					<h2> {formatMessage('navigation.projects')}</h2>
					<FilmFeed data={data.projects} isHome />

					<h2> {formatMessage('navigation.news')}</h2>

					<PostFeed data={data} isHome />
				</div>
			</Layout>
		</>
	);
};
export default NewIndex;

export const newIndexQuery = graphql`
	query NewIndex($locale: String!) {
		projects: allMdx(
			sort: { fields: [frontmatter___featureOrder], order: ASC }
			filter: { fields: { locale: { eq: $locale }, category: { eq: "filmography" } }, frontmatter: { featured: { eq: true } } }
		) {
			nodes {
				fields {
					slug
					category
				}
				frontmatter {
					title
					director
					release(formatString: "DD MMMM YYYY")
					tags
					imdb
					image {
						childImageSharp {
							gatsbyImageData(layout: FULL_WIDTH, placeholder: TRACED_SVG)
						}
					}
				}
				excerpt(truncate: true, pruneLength: 200)
				timeToRead
			}
		}
		posts: allMdx(
			sort: { fields: [frontmatter___date], order: DESC }
			limit: 4
			filter: { fields: { locale: { eq: $locale }, category: { eq: "posts" } }, frontmatter: { draft: { ne: true } } }
		) {
			nodes {
				fields {
					slug
					category
				}
				frontmatter {
					date(formatString: "DD MMMM YYYY")
					title
					featured
					tags
					image {
						childImageSharp {
							gatsbyImageData(layout: FULL_WIDTH, placeholder: TRACED_SVG)
						}
					}
				}
				excerpt(truncate: true, pruneLength: 200)
				timeToRead
			}
		}
	}
`;
