import * as React from 'react';
//import { Index_posts_nodes as Post } from '~src/pages/__generated__/Index';
//import AuthorList from '~src/components/Sections/AuthorList/AuthorList';
import Link from '~src/_i18n/components/Link';
import useIntlHelper from '~src/_i18n/helpers/useIntlHelper';
import ImgSharp from '~src/components/utils/ImgSharp/ImgSharp';
import * as styles from './FilmCard.module.scss';
import { Film } from '~src/types/Film';
//import { IMDB } from '~src/_assets/icons/social/imdb';

export const FilmCard: React.FC<{
	film: Film;
	i?: number;
	isHome?: boolean;
}> = ({ film, i, isHome }) => {
	const { formatMessage, formatDate } = useIntlHelper();
	const isFeatured = film.frontmatter.featured;
	return (
		<article className={styles.postCard + ` ` + ((isHome && isFeatured && 0 === i % 6 && styles.postCardLarge) || ``)}>
			<Link className={styles.postCardImageLink} to={`/${film.fields.category}/${film.fields.slug}`}>
				<ImgSharp
					className={styles.postCardImage}
					alt={film.frontmatter.title}
					/* 	sizes="(max-width: 1000px) 400px, 700px" */
					image={film.frontmatter.image.childImageSharp.gatsbyImageData}
				/>
			</Link>
			<div className={styles.postCardContent}>
				<Link className={styles.postCardContentLink} to={`/${film.fields.category}/${film.fields.slug}`}>
					<header className={styles.postCardHeader}>
						<div className={styles.postCardPrimaryTag}>
							{film.frontmatter.tags[0] !== undefined && formatMessage(`tags.${film.frontmatter.tags[0]}.title`)}
						</div>
						<h2 className={styles.postCardTitle}>{film.frontmatter.title}</h2>
					</header>

					<section className={styles.postCardBylineContent}>
						<span>
							{formatMessage(`projects.director`)} : {film.frontmatter.director}
							{/* 	<Link to={`/authors/${post.frontmatter.authors[0].id}/`}>
								{intl.formatMessage({
									id: `authors.${post.frontmatter.authors[0].id}.title`,
								})}
							</Link> */}
						</span>
						<br />
						<span>
							{formatMessage(`projects.release`)} : <time dateTime={film.frontmatter.release}>{formatDate(film.frontmatter.release)}</time>
							{/* <span className="bull">•</span> {intl.formatNumber(film.timeToRead)} {intl.formatMessage({ id: 'posts.timeToRead' })} */}
						</span>
					</section>
				</Link>
				<footer className={styles.postCardMeta}>
					{/* 	<AuthorList authors={post.frontmatter.authors} /> */}

					<div className={styles.postCardExcerpt}>
						{/* 						<p>{film.excerpt}</p> */}
						{/* 						<a
							className={`${styles.socialLink} ${styles.socialImdb}`}
							href={`https://www.imdb.com/title/${film.frontmatter.imdb}/`}
							title="IMDB"
							target="_blank"
							rel="noopener noreferrer">
							<IMDB />
						</a> */}
					</div>
				</footer>
			</div>
		</article>
	);
};
