import * as React from 'react';
import classnames from 'classnames';
import Link from '~src/_i18n/components/Link';
import { Navigation } from '~src/components/Layout/Navigation/Navigation';

import * as styles from './NewHero.module.scss';

import HoverVideoPlayer from 'react-hover-video-player';
import animationVideo from '~src/_assets/videos/HeroAnimationVideo.mp4';
import vfxVideo from '~src/_assets/videos/HeroVFXVideo.mp4';
import animationPic from '~src/_assets/videos/HeroAnimation_thumb.png';
import vfxPic from '~src/_assets/videos/HeroVFX_thumb.png';
import useIntlHelper from '~src/_i18n/helpers/useIntlHelper';

export const NewHero = () => {
	const { formatMessage } = useIntlHelper();

	const wrapperVFX = React.useRef();
	const wrapperAnimation = React.useRef();
	return (
		<div className={styles.wrapper}>
			<div className="outer">
				<div className="inner">
					<Navigation className="site-nav" isHome={true} />
				</div>
			</div>
			<div /* className={styles.container} */>
				<div className={styles.component}>
					<Link to="/animation">
						<div className={classnames(styles.slide, styles.animation)} ref={wrapperAnimation}>
							<HoverVideoPlayer
								videoSrc={animationVideo}
								className={styles.videoWrapper}
								hoverTarget={wrapperAnimation}
								// The video and overlays should expand to fill the 16:9 container
								sizingMode="container"
								pausedOverlay={
									<img
										src={animationPic}
										alt=""
										style={{
											// Make the image expand to cover the video's dimensions
											width: '100%',
											height: '100%',
											objectFit: 'cover',
											opacity: '0.9',
										}}
									/>
								}
								loadingOverlay={<div className="loading-spinner-overlay" />}
							/>
							<h2 className={styles.title}>{formatMessage('hero.animation')}</h2>
							<p className={styles.paragraph}>{formatMessage('hero.animationText')}</p>
						</div>
					</Link>
					<Link to="/vfx">
						<div className={classnames(styles.slide, styles.vfx)} ref={wrapperVFX}>
							<HoverVideoPlayer
								videoSrc={vfxVideo}
								className={styles.videoWrapper}
								hoverTarget={wrapperVFX}
								sizingMode="container"
								pausedOverlay={
									<img
										src={vfxPic}
										alt=""
										style={{
											// Make the image expand to cover the video's dimensions
											width: '100%',
											height: '100%',
											objectFit: 'cover',
											opacity: '0.9',
										}}
									/>
								}
								loadingOverlay={<div className="loading-spinner-overlay" />}
							/>
							<h2 className={styles.title}>{formatMessage('hero.vfx')}</h2>
							<p className={styles.paragraph}>{formatMessage('hero.vfxText')}</p>
						</div>
					</Link>
				</div>
			</div>
		</div>
	);
};
