import * as React from 'react';
import { Index_projects } from '~src/pages/__generated__/Index';
import { Film } from '~src/types/Film';
import Link from '~src/_i18n/components/Link';
import useIntlHelper from '~src/_i18n/helpers/useIntlHelper';
import { FilmCard } from '../FilmCard/FilmCard';
import * as styles from './FilmFeed.module.scss';

export const FilmFeed: React.FC<{ data: Index_projects; isHome?: boolean }> = ({ data, isHome }) => {
	const { formatMessage } = useIntlHelper();
	return (
		<div className={styles.posts}>
			<div className={styles.postFeed}>
				{(data.nodes as Film[]).map((film: Film, i: number) => {
					return <FilmCard film={film} i={i} key={i} />;
				})}
			</div>
			{isHome ? (
				<Link to="/filmography">
					<h3 className={styles.readMore}> {isHome ? formatMessage('general.allProjects') : null}</h3>
				</Link>
			) : null}
		</div>
	);
};
